import React, {useContext} from "react";
import {DropdownRefContext} from "../../Providers/index";
import {DropdownTriggerProps} from "../Dropdown.types";

const Tippy = require("@tippyjs/react/headless").default;
const cn = require('classnames');

const DropdownTrigger: React.FC<DropdownTriggerProps> = (props) => {
  const {children, className, trigger = 'click', interactive, placement = 'bottom', menu, onClick, visible, hideOnClick, showOnCreate, ...rest} = props;
  const containerRef = useContext(DropdownRefContext);
  let _trigger = trigger;
  let _hideOnClick = hideOnClick;
  let _showOnCreate = showOnCreate;

  if (props.visible !== undefined) {
    _trigger = undefined;
    _hideOnClick = undefined;
    _showOnCreate = undefined;
  }

  return (
      <Tippy
          render={attrs => <div {...attrs} className={cn(className)}>{menu}</div>}
          appendTo={containerRef || document.body}
          trigger={_trigger}
          interactive={typeof interactive === 'boolean' ? interactive : true}
          placement={placement}
          visible={visible}
          hideOnClick={_hideOnClick}
          showOnCreate={_showOnCreate}
          {...rest}
      >
        { React.cloneElement(children, { onClick }) }
      </Tippy>
  );
}

export default DropdownTrigger;
